import React from 'react';
import CopyrightIcon from '@material-ui/icons/Copyright';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const Footer = ({ classes, copyrightText, url, websiteText }) => (
  <div className={classes.root}>
    <Grid container justify="center" alignContent="center" spacing={16} className={classes.grid}>
      <Grid item xs={12} sm={6}>
        <div className={classes.copyrightContainer}>
          <CopyrightIcon />
          <Typography variant="body1" className={classes.copyright}>{copyrightText}</Typography>
        </div>
      </Grid>
      <Grid item xs={12} sm={6}>
        <Typography variant="body1" component="a" href={url} target="_blank" className={classes.website} align="right">{websiteText}</Typography>
      </Grid>
    </Grid>
  </div>
);

Footer.propTypes = propTypes;
Footer.defaultProps = defaultProps;

export default withStyles(styles)(Footer);
