import React from 'react';
import DoneIcon from '@material-ui/icons/Done';

import CompactListItem from '../../../Common/CompactListItem';
import propTypes, { defaultProps } from './propTypes';

const RemovalListItem = ({ text }) => <CompactListItem icon={() => <DoneIcon />} text={text} />;

RemovalListItem.propTypes = propTypes;
RemovalListItem.defaultProps = defaultProps;

export default RemovalListItem;
