import React from 'react';
import List from '@material-ui/core/List';

import RemovalListItem from './RemovalListItem';
import removalSrc from './removal.jpg';
import ServiceCard from '../ServiceCard';

import propTypes, { defaultProps } from './propTypes';

const src = removalSrc;

const RemovalCard = ({ title, items }) => (
  <ServiceCard title={title} image={{ title, src }}>
    <List>
      {items.map((item, key) => (
        <RemovalListItem key={key} text={item} />
      ))}
    </List>
  </ServiceCard>
);

RemovalCard.propTypes = propTypes;
RemovalCard.defaultProps = defaultProps;

export default RemovalCard;
