import React from 'react';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import logoSrc from './logo.png';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const MainAppBar = ({ classes, className, title1, title2, logoAlt }) => (
  <div className={classes.root+ ' '+ className}>
    <img src={logoSrc} alt={logoAlt} className={classes.logoImage} />
    <div>
      <Typography variant="h1" className={classes.title1} color="primary">
        {title1}
      </Typography>
      <Typography variant="h1" className={classes.title2} color="secondary">
        {title2}
      </Typography>
    </div>
  </div>
);

MainAppBar.propTypes = propTypes;
MainAppBar.defaultProps = defaultProps;

export default withStyles(styles)(MainAppBar);
