import React from 'react';
import Grid from '@material-ui/core/Grid';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import EmailListItem from './EmailListItem';
import PhoneListItem from './PhoneListItem';
import PlaceListItem from './PlaceListItem';
import mapSrc from './map.png';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const ContactSection = ({ classes, title, contactText, phone, email, placesText, places, mapAlt }) => (
  <div className={classes.root}>
    <Typography variant="h2" className={classes.heading} color="primary">
      {title}
    </Typography>
    <Grid container justify="center" spacing={16} className={classes.grid}>
      <Grid item xs={12} sm={6}>
        <div className={classes.contactContainer}>
          <Typography variant="h5">{contactText}</Typography>
          <List>
            <PhoneListItem text={phone} />
            <EmailListItem text={email} />
          </List>
        </div>
        <div className={classes.areaContainer}>
          <Typography>{placesText}</Typography>
          <List>
            {places.map((place, key) => (
              <PlaceListItem key={key} text={place} />
            ))}
          </List>
        </div>
      </Grid>
      <Grid item xs={12} sm={6}>
        <img src={mapSrc} alt={mapAlt} className={classes.mapImage} />
      </Grid>
    </Grid>
  </div>
);

ContactSection.propTypes = propTypes;
ContactSection.defaultProps = defaultProps;

export default withStyles(styles)(ContactSection);
