import React from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const ServiceCard = ({ classes, image, title, children }) => (
  <Card className={classes.card}>
    <CardMedia component="img" alt={image.title} className={classes.media} height="240" image={image.src} title={image.title} />
    <CardContent className={classes.content}>
      <Typography gutterBottom variant="h5" component="h2">
        {title}
      </Typography>
      {children}
    </CardContent>
  </Card>
);

ServiceCard.propTypes = propTypes;
ServiceCard.defaultProps = defaultProps;

export default withStyles(styles)(ServiceCard);
