import React from 'react';
import Typography from '@material-ui/core/Typography';

import cleanUpSrc from './cleanUp.jpg';
import ServiceCard from '../ServiceCard';

import propTypes, { defaultProps } from './propTypes';

const src = cleanUpSrc;

const CleanupCard = ({ title, text }) => (
  <ServiceCard title={title} image={{ title, src }}>
    <Typography>{text}</Typography>
  </ServiceCard>
);

CleanupCard.propTypes = propTypes;
CleanupCard.defaultProps = defaultProps;

export default CleanupCard;
