import React from 'react';
import Typography from '@material-ui/core/Typography';

import bobcatSrc from './bobcat.jpg';
import ServiceCard from '../ServiceCard';

import propTypes, { defaultProps } from './propTypes';

const src = bobcatSrc;

const BobcatHireCard = ({ title, text }) => (
  <ServiceCard title={title} image={{ title, src }}>
    <Typography>{text}</Typography>
  </ServiceCard>
);

BobcatHireCard.propTypes = propTypes;
BobcatHireCard.defaultProps = defaultProps;

export default BobcatHireCard;
