export default theme => ({
  root: {
    display: 'flex',
  },
  logoImage: {
    height: 85,
    margin: theme.spacing.unit,
  },
  title1: {
    fontSize: '3rem',
    fontFamily: '"Titan One", Arial, sans-serif',
    textShadow: '0 0 50px #ffffff',
    [theme.breakpoints.only('xs')]: {
      fontSize: '2.5rem',
    },
  },
  title2: {
    fontSize: '3rem',
    fontWeight: 400,
    [theme.breakpoints.only('xs')]: {
      fontSize: '2.2rem',
    },
  },
});
