import React from 'react';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';

import BobcatHireCard from './BobcatHireCard';
import CleanupCard from './CleanupCard';
import RemovalCard from './RemovalCard';

import propTypes, { defaultProps } from './propTypes';
import styles from './styles';

const ServicesSection = ({ classes, bobcat, cleanUp, removal, fullyInsuredText }) => (
  <div className={classes.root}>
    <Typography variant="h2" className={classes.heading} color="primary">
      Services
    </Typography>
    <Typography variant="h6" className={classes.fullyInsured}>
      {fullyInsuredText}
    </Typography>
    <Grid container direction="row" justify="center" spacing={16} className={classes.grid}>
      <Grid item xs={12} sm={4}>
        <CleanupCard {...cleanUp} />
      </Grid>
      <Grid item xs={12} sm={4}>
        <RemovalCard {...removal} />
      </Grid>
      <Grid item xs={12} sm={4}>
        <BobcatHireCard {...bobcat} />
      </Grid>
    </Grid>
  </div>
);

ServicesSection.propTypes = propTypes;
ServicesSection.defaultProps = defaultProps;

export default withStyles(styles)(ServicesSection);
